import React, { useState } from 'react';
import { TextBox, Button } from '../../components/common';
import S from './Login.styles';
import { useLocation, useNavigate } from 'react-router-dom';
import { apiLogin } from '../../api/api';
import { supabase } from '../../supabaseClient';

const LoginPage = () => {
	const contentWidth = '30%';

	const location = useLocation();
	const navigate = useNavigate();

	const from = location.state?.from?.pathname || '/';

	const [inputs, setInputs] = useState({
		email: '',
		password: '',
	});

	const { email, password } = inputs;

	const [isLoading, setIsLoading] = useState(false);
	
	const [validation, setValidation] = useState({
		email: { status: 'defualt', message: '' },
		password: { status: 'default', message: '' },
	});
	
	const handleChange = (e) => {
        const { id, value } = e.target;
        
        // Input  
        setInputs((prev) => ({
            ...prev,
            [id]: value,
        }));
        
        //     / 
        setValidation({
            email: { status: 'default', message: '' },
            password: { status: 'default', message: '' },
        });
    };

	const handleLoginClick = async () => {
        if (!email || !password) {
            setValidation({
                email: {
                    status: !email ? 'error' : 'default',
                    message: !email ? ' .' : '',
                },
                password: {
                    status: !password ? 'error' : 'default',
                    message: !password ? ' .' : '',
                }
            });
            return;
        }
		setIsLoading(true);

		try {
			const data = await apiLogin(email, password);
			const { error } = supabase.auth.setSession(data.session);

			if (error) {
				throw new Error(error.message);
			}

			alert(' ');
			navigate(from, { replace: true }); //    
		} catch(err) {
			if (err.message === "Email not confirmed") {
				// Supabase    .
				alert("  .     .");
			} else {
				console.error(' :', err);
				setValidation({
					email: { status: 'error', message: '    .' },
					password: { status: 'error', message: ' ' } 
				});
			}
		} finally {
			setIsLoading(false);
		}
	};
	
	return (
		<S.Screen>
			<S.Title></S.Title>
			<S.ContentWrapper>
				<TextBox
					label={'email'}
					id={'email'}
					width={contentWidth}
					value={email}
					onChange={handleChange}
					caption={validation.email.message}
					status={validation.email.status}
				/>
				<TextBox
					label={'password'}
					id={'password'}
					width={contentWidth}
					type={'password'}
					value={password}
					onChange={handleChange}
					caption={validation.password.message}
					status={validation.password.status}
				/>
				<div style={{height: '3rem'}} ></div>
				<Button
					variant='solid'
					width={contentWidth}
					onClick={handleLoginClick}
					disable={isLoading}
				>
					
				</Button>
			</S.ContentWrapper>
		</S.Screen>
	);
};

export default LoginPage;