import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { useNavigate, useLocation } from 'react-router-dom';
import { ChevronRight, ChevronDown } from 'react-bootstrap-icons';
import S from '../MyPage.styles'; 
import { getHistoryList } from '../../../api/api'; 
import { useAuth } from '../../../contexts/AuthContext';

const HistoryPage = () => {
    const navigate = useNavigate();
    const location = useLocation();
    const { session } = useAuth();

    // --- State ---
    const [histories, setHistories] = useState([]);
    const [loading, setLoading] = useState(false); //  false (useEffect true )
    const [page, setPage] = useState(1);
    const [totalCount, setTotalCount] = useState(0);

    const LIMIT = 10;

    // --- Data Fetching ---
    const fetchHistories = async (pageNum) => {
        if (!session) return;
        setLoading(true);

        try {
            const response = await getHistoryList(session.access_token, pageNum, LIMIT);
            const newData = response.list || []; 
            const total = response.totalCount || 0;

            setTotalCount(total);

            if (pageNum === 1) {
                setHistories(newData);
            } else {
                setHistories(prev => [...prev, ...newData]);
            }
        } catch (err) {
            console.error("History fetch error:", err.message);
        } finally {
            setLoading(false);
        }
    };

    // 1.   
    useEffect(() => {
        setPage(1);
        fetchHistories(1);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [session, location.key]);

    // 2.  
    const handleLoadMore = () => {
        const nextPage = page + 1;
        setPage(nextPage);
        fetchHistories(nextPage);
    };

    // --- Helper Functions ---
    const formatTime = (dateString) => {
        const date = new Date(dateString);
        const now = new Date();
        const diffMin = Math.floor((now - date) / 1000 / 60);

        if (diffMin < 1) return ' ';
        if (diffMin < 60) return `${diffMin} `;
        const diffHour = Math.floor(diffMin / 60);
        if (diffHour < 24) return `${diffHour} `;
        const diffDay = Math.floor(diffHour / 24);
        if (diffDay < 7) return `${diffDay} `;
        return `${date.getMonth() + 1} ${date.getDate()}`;
    };

    const formatSpecSummary = (specsArray) => {
        if (!specsArray || specsArray.length === 0) return ' ';
        const text = specsArray.join(', ');
        return text.length > 30 ? text.substring(0, 30) + '...' : text;
    };

    const handleItemClick = (id) => {
        navigate(`/mypage/history/${id}`);
    };

    return (
        <S.ContentWrapper>
            <S.SubTitle> </S.SubTitle>
            
            <ListContainer>
                {/* [CASE 1]     (   )
                    -> ""   
                */}
                {loading && histories.length === 0 ? (
                    <LoadingWrapper>
                        <LoadingText>...</LoadingText>
                    </LoadingWrapper>
                ) : (
                    /* [CASE 2]  ,    */
                    <>
                        {/*   */}
                        {histories.length > 0 ? (
                            histories.map(item => (
                                <HistoryItem key={item.id} onClick={() => handleItemClick(item.id)}>
                                    <Time>{formatTime(item.created_at)}</Time>
                                    <ItemTitle>{item.title}</ItemTitle>
                                    <ItemDesc>
                                        <span className="label">  : </span> 
                                        {formatSpecSummary(item.specsSummary)}
                                    </ItemDesc>
                                    <ArrowIcon><ChevronRight /></ArrowIcon>
                                </HistoryItem>
                            ))
                        ) : (
                            /*    (  &&  0) */
                            <StateMsg>   .</StateMsg>
                        )}

                        {/* [CASE 3]     ()
                            ->   " ..." 
                        */}
                        {loading && histories.length > 0 && (
                            <LoadingText> ...</LoadingText>
                        )}

                        {/*    */}
                        {!loading && histories.length < totalCount && (
                            <LoadMoreButton onClick={handleLoadMore}>
                                10   <ChevronDown />
                            </LoadMoreButton>
                        )}
                    </>
                )}
            </ListContainer>
        </S.ContentWrapper>
    );
};

export default HistoryPage;

/* --- Local Styles --- */

const ListContainer = styled.div`
    padding: 2rem;
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding-bottom: 60px;
    min-height: 200px; //     
`;

const LoadingWrapper = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px; //   
    width: 100%;
`;

const LoadingText = styled.div`
    text-align: center;
    color: ${({ theme }) => theme.textSub};
    font-size: 0.95rem;
    padding: 20px;
`;

const HistoryItem = styled.button`
    width: 100%;
    text-align: left;
    background-color: transparent;
    padding: 24px;
    border-radius: 16px;
    position: relative;
    cursor: pointer;
    border: 1px solid transparent; 

    &:hover {
        background-color: ${({ theme }) => theme.backgroundSub};
    }
`;

const LoadMoreButton = styled.button`
    width: 100%;
    padding: 16px;
    margin-top: 12px;
    border-radius: 12px;
    background-color: ${({ theme }) => theme.backgroundSub}; 
    color: ${({ theme }) => theme.textSub};
    font-size: 0.95rem;
    font-weight: 500;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    transition: all 0.2s ease;
    border: none;

    &:hover {
        background-color: ${({ theme }) => theme.border};
        color: ${({ theme }) => theme.text};
    }
`;

const Time = styled.div`
    font-size: 0.8rem;
    color: ${({ theme }) => theme.textSub};
    margin-bottom: 8px;
`;

const ItemTitle = styled.div`
    font-size: 1.1rem;
    font-weight: 700;
    color: ${({ theme }) => theme.text};
    margin-bottom: 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 20px; 
`;

const ItemDesc = styled.div`
    font-size: 0.9rem;
    color: ${({ theme }) => theme.text};
    font-weight: 400;

    .label {
        color: ${({ theme }) => theme.text};
        font-size: 0.85rem;
    }
`;

const ArrowIcon = styled.div`
    position: absolute;
    right: 24px;
    top: 50%;
    transform: translateY(-50%);
    color: ${({ theme }) => theme.main.regular}; 
    font-size: 20px;
    display: flex; 
    align-items: center;
`;

const StateMsg = styled.div`
    text-align: center;
    color: ${({ theme }) => theme.textSub};
    padding: 60px 0;
    font-size: 0.95rem;
`;