import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { InfoCircleFill } from 'react-bootstrap-icons'; //  
import { getCategories } from '../../../api/api'; // API   
import { Button } from '../../../components/common';

const CategorySelectModal = ({ onSelect }) => {
	const [categories, setCategories] = useState([]);
	const [isLoading, setIsLoading] = useState(true);
	
	//     (   )
	const [tempSelected, setTempSelected] = useState(null);

	useEffect(() => {
		const fetchData = async () => {
		setIsLoading(true);
		try {
			const data = await getCategories();
			console.log(data);
			if (Array.isArray(data)) {
				setCategories(data);
			} else {
				console.error("  :", data);
				setCategories([]); //    
				// setErrorMsg("   .");
			}
		} catch (error) {
			console.error(error);
		} finally {
			setIsLoading(false);
		}
		};
		fetchData();
	}, []);

	//     (  )
	const handleRadioChange = (categorySlug) => {
		setTempSelected(categorySlug);
	};

	//     ( )
	const handleConfirm = () => {
		if (tempSelected) {
			// 1.   slug     .
			const selectedCategoryObj = categories.find(c => c.slug === tempSelected);

			if (selectedCategoryObj) {
				// 2.  { slug, name }  
				onSelect({
					id: selectedCategoryObj.id,
					slug: selectedCategoryObj.slug,
					name: selectedCategoryObj.name,
					specs: selectedCategoryObj.specs,
				});
			}
		} else {
			alert(" .");
		}
	};

	return (
		<Overlay>
		<ModalContainer>
			<ModalHeader>
			<h2></h2>
			</ModalHeader>
			
			<ModalBody>
			{isLoading ? (
				<LoadingMessage>  ...</LoadingMessage>
			) : (
				<RadioGroup>
				{ categories && categories.length > 0 ? categories.map((category) => (
					<RadioLabel key={category.id} isSelected={tempSelected === category.slug}>
					<RadioInput
						type="radio"
						name="category"
						value={category.slug}
						checked={tempSelected === category.slug}
						onChange={() => handleRadioChange(category.slug)}
					/>
					<CustomRadio />
					<LabelText>{category.name}</LabelText>
					</RadioLabel>
				)) : (
					<div>  .</div>
				)}
				</RadioGroup>
			)}

			{/*  :    */}
			<InfoMessage>
				<InfoIconWrapper>
				<InfoCircleFill size={16} />
				</InfoIconWrapper>
				<span>    .</span>
			</InfoMessage>
			</ModalBody>
			
			<ModalFooter>
			{/*    */}
			<Button
				onClick={handleConfirm} 
				disabled={!tempSelected || isLoading} //   
			>
				
			</Button>
			</ModalFooter>
		</ModalContainer>
		</Overlay>
	);
};

export default CategorySelectModal;

// --- Styled Components ---

const Overlay = styled.div`
	position: fixed;
	top: 0; left: 0; width: 100%; height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	display: flex; justify-content: center; align-items: center;
	z-index: 1000;
`;

const ModalContainer = styled.div`
	width: 300px;
	background-color: ${({ theme }) => theme.background};
	border-radius: 16px;
	overflow: hidden;
	box-shadow: 0 4px 20px ${({ theme }) => theme.shadow};
	display: flex;
	flex-direction: column;
`;

const ModalHeader = styled.div`
	background-color: ${({ theme }) => theme.main.regular}; /*    */
	padding: 20px;
	text-align: center;
	
	h2 {
		margin: 0;
		color: white;
		font-size: 1.4rem;
		font-weight: 700;
	}
`;

const ModalBody = styled.div`
	padding: 24px 20px;
	display: flex;
	flex-direction: column;
	gap: 20px;
	min-height: 200px;
`;

const RadioGroup = styled.div`
	display: flex;
	flex-direction: column;
	gap: 12px;
`;

const RadioLabel = styled.label`
	display: flex;
	align-items: center;
	cursor: pointer;
	padding: 10px 12px;
	border-radius: 8px;
	transition: background-color 0.2s;
	background-color: ${({ isSelected, theme }) => isSelected ? theme.backgroundSub : 'transparent'};
	
	&:hover {
		background-color: ${({ theme }) => theme.backgroundSub};
	}
`;

const RadioInput = styled.input`
  	display: none;
`;

const CustomRadio = styled.div`
	width: 20px; height: 20px;
	border: 2px solid #d1d5db;
	border-radius: 50%;
	margin-right: 12px;
	position: relative;
	flex-shrink: 0;

	${RadioInput}:checked + & {
		border-color: ${({ theme }) => theme.main.regular}; /*    */
		
		&::after {
			content: '';
			position: absolute;
			top: 50%; left: 50%;
			transform: translate(-50%, -50%);
			width: 10px; height: 10px;
			background-color: ${({ theme }) => theme.main.regular}; /*    */
			border-radius: 50%;
		}
  }
`;

const LabelText = styled.span`
	font-size: 1rem;
	color: ${({ theme }) => theme.text};
	font-weight: 500;
`;

const InfoMessage = styled.div`
	display: flex;
	align-items: flex-start; 
	gap: 8px;
	font-size: 0.85rem;
	color: ${({ theme }) => theme.textSub};
	margin-top: auto;
	line-height: 1.4;
	border: 2px solid ${({ theme }) => theme.backgroundSub};
	padding: 12px;
	border-radius: 8px;
`;

const InfoIconWrapper = styled.div`
	color: ${({ theme }) => theme.sub};
	display: flex;
	align-items: center;
	margin-top: 2px;
`;

const ModalFooter = styled.div`
	padding: 0 20px 24px 20px;
`;

const LoadingMessage = styled.div`
	text-align: center;
	color: ${({ theme }) => theme.textSub};
	padding: 40px 0;
`;